import Quagga from 'quagga';

export default {

    options: {
        locate: true,
        decoder: {
            readers: ['ean_reader'],
        },
    },

    async decode(data) {
        return await new Promise((resolve, reject) => {
            Quagga.decodeSingle({
                ...this.options,
                src: data,
            },
            (result) => {
                if (!result.codeResult) {
                    reject();
                }

                resolve(result);
            });
        });
    },

    async decodeStream(targetElement) {
        return await new Promise((resolve, reject) => {
            Quagga.init({
                ...this.options,
                inputStream: {
                    name : "Live",
                    type : "LiveStream",
                    target: targetElement,
                },
            },
            (error) => {
                if (error) {
                    reject(error);
                }

                Quagga.start();

                Quagga.onDetected((result) => {
                    Quagga.stop();
                    resolve(result);
                });
            });
        });
    },
}