<template>
    <div class="barcode-scanner-view">
        <HeaderComponent>
        </HeaderComponent>
        <div class="barcode-scanner-container">
            <h1>Scan a Barcode</h1>
            <p>
                <input ref="fileInput" type="file" @change="onDecodeImageChange">
            </p>
            <div class="scanning-container" ref="scanningContainer"></div>
        </div>
    </div>
</template>

<script>
    import API from '@/api/API.js';
    import BarcodeService from '@/service/BarcodeService.js';

    import HeaderComponent from '@/component/page/HeaderComponent.vue';
    import ButtonComponent from '@/component/item/ButtonComponent.vue';

    export default {
        name: 'BarcodeScannerView',

        components: {
            HeaderComponent,
            ButtonComponent,
        },

        async mounted() {
            await BarcodeService.decodeStream(this.$refs.scanningContainer);
        },

        methods: {
            async onDecodeImageChange() {
                if (this.$refs.fileInput.files.length === 0) {
                    return;
                }

                const file = this.$refs.fileInput.files[0];
                const data = URL.createObjectURL(file);

                try {
                    const result = await BarcodeService.decode(data);
                    const productCode = result.codeResult.code;

                    const product = await API.getProductFromBarcode(productCode);

                    console.log(product.data.result);
                }
                catch (error) {
                    console.log('Unable to decode barcode.');
                }
            },
        },
    }
</script>

<style lang="scss">
    .barcode-scanner-view {
        .barcode-scanner-container {
            padding: 0 1rem;
        }

        .barcode-button {
            width: 100%;
        }

        .scanning-container {
            width: 100%;
            height: 50vh;
            border-radius: layout(border-radius);
            box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
            background-color: theme(black);
        }
    }
</style>